* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box !important; }

.navbar {
  background-color: lightblue;
  width: 100%;
  min-height: 50px;
  padding: 5px; }
  .navbar span {
    color: black;
    font-size: 20px;
    margin: 20px; }
    .navbar span:hover {
      color: red; }
  .navbar a {
    text-decoration: none; }

.Dashboard {
  width: 100%;
  height: auto;
  clear: both;
  margin: 20px; }
  .Dashboard .ProjectList {
    height: auto;
    width: 50%;
    float: left; }
    .Dashboard .ProjectList .project_summary {
      min-height: 300px;
      background-color: lightgray; }
      .Dashboard .ProjectList .project_summary .project_title {
        color: red;
        font-weight: bold; }
      .Dashboard .ProjectList .project_summary .project_name {
        color: bisque;
        text-decoration: underline; }
      .Dashboard .ProjectList .project_summary .project_time {
        color: lightgray;
        font-style: italic; }
  .Dashboard .Notification {
    background-color: lightgoldenrodyellow;
    width: 50%;
    color: red;
    font-weight: bold;
    height: auto;
    float: right; }

.ProjectDetails {
  background-color: lightpink;
  margin: 20px;
  box-sizing: border-box; }
  .ProjectDetails .project_id {
    color: black;
    font-weight: bold;
    font-size: 20px; }
  .ProjectDetails .posted_by {
    color: green;
    font-weight: bold;
    font-size: 20px; }

ul {
  display: inline-block;
  margin: 15px; }
  ul li {
    display: inline-block;
    text-transform: uppercase; }
    ul li a {
      display: inline-block;
      margin: 15px;
      cursor: pointer; }

.authError {
  background-color: red;
  color: white;
  font-size: 20px;
  margin-top: 20px; }

.welcome {
  font-size: 20px;
  color: darkblue;
  font-weight: bold; }

.postPerson {
  font-size: 20px;
  font-weight: bold;
  color: brown; }

.post_time {
  color: blue;
  font-weight: bold;
  font-size: 20px; }

.notification_list {
  display: block;
  text-transform: lowercase; }
  .notification_list .username {
    color: black; }
  .notification_list .action_time {
    color: gray;
    text-decoration: underline;
    font-size: 15px;
    font-style: italic; }
